<template>
  <div>
    <ItemCategoryListHierarchy
      :itemCategoryHierarchyList="itemCategoryHierarchyList"
    />
  </div>
</template>

<script>
import ItemCategoryListHierarchy from "@/components/diablo2/ItemCategoryListHierarchy.vue";

import itemCategoryList from "@/assets/diablo2/data/itemCategory.json";

export default {
  components: { ItemCategoryListHierarchy },
  data() {
    return { itemCategoryList, itemCategoryHierarchyList: [] };
  },
  mounted() {
    this.getItemCategoryHierarchyList();
  },
  methods: {
    // itemCategoryList를 기반으로 hierarchy list 목록을 만들어 반환한다.
    getItemCategoryHierarchyList() {
      if (this.itemCategoryHierarchyList.length > 0) {
        return this.itemCategoryHierarchyList;
      }

      this.itemCategoryHierarchyList = this.getSubItemCategoryList([], null);
    },

    getSubItemCategoryList(list, categoryName) {
      for (var i = 0; i < this.itemCategoryList.length; i++) {
        if (this.itemCategoryList[i].parentName == categoryName) {
          list.push(this.itemCategoryList[i]);
        }
      }

      if (list.length > 0) {
        for (var j = 0; j < list.length; j++) {
          var subItemCategoryList = this.getSubItemCategoryList(
            [],
            list[j].name
          );
          list[j].subItemCategoryList = subItemCategoryList;
        }
      }
      return list;
    },
  },
};
</script>

